import { Component, Input } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';

import { PostPage } from '../../pages/post/post';

import { AppConfig } from '../../app/app.config';

/**
 * Generated class for the PostComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'post',
  templateUrl: 'post.html'
})
export class PostComponent {  
  @Input()
  post = null;

  @Input()
  postAddress = '';

  @Input()
  curPostTime = '';

  @Input()
  isFull = false;

  comments = [
    "I almost agree with you!",
    "I almost agree with you!",
    "I almost agree with you!",
  ];

  newComment = '';

  constructor(private navCtrl: NavController, private alertCtrl: AlertController) {
    
  }

  onClickLikeButton() {
    let like = this.post.is_like ? 0 : 1;

    this.post.is_like = like;

    let url = '';

    if (like) {
      url = AppConfig.getUrl() + '/api/like/' + this.post.id;
      this.post.total_likes_count++;
    } else {
      url = AppConfig.getUrl() + '/api/unlike/' + this.post.id;
      this.post.total_likes_count--;
    }
  }

  onViewButtonClick() {
    console.log('click view button');
    this.displayFullPostPage();
  }

  onCommentButtonClick() {
    this.displayFullPostPage();
  }

  displayFullPostPage() {
    this.navCtrl.push(PostPage, {
      post: this.post,
      postAddress: this.postAddress,
      curPostTime: this.curPostTime
    });
  }

  onClickSubmitCommentButton() {
    if (this.newComment == '') {
      this.showAlert();
      return;
    }
    
    this.comments.unshift(this.newComment);
    this.newComment = '';
    this.post.comments_count++;
  }

  showAlert() {
    const alert = this.alertCtrl.create({
      title: 'Create New Comment Fail!',
      subTitle: 'Comment can not be empty!',
      buttons: ['OK']
    });
    alert.present();
  }

}



