import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { UserHelpPage } from '../user-help/user-help';
import { UserPrivacyStatementPage } from '../user-privacy-statement/user-privacy-statement';

/**
 * Generated class for the UserPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-user',
  templateUrl: 'user.html',
})
export class UserPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserPage');
  }

  onClickSettingsButton() {
    this.navCtrl.push('SettingsPage');
  }

  onClickLogoutButton() {
    this.navCtrl.push('WelcomePage');
  }

  onClickHelpButton() {
    this.navCtrl.push(UserHelpPage);
  }

  onClickPrivacyStatementButton() {
    this.navCtrl.push(UserPrivacyStatementPage);
  }

}



