import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { HomePage } from '../pages/home/home';
import { PassPage } from '../pages/pass/pass';
import { NotificationPage } from '../pages/notification/notification';
import { UserPage } from '../pages/user/user';
import { TabsPage } from '../pages/tabs/tabs';
import { PostPage } from '../pages/post/post';
import { NotificationDetailPage } from '../pages/notification-detail/notification-detail';
import { ForgotPasswordPage } from '../pages/forgot-password/forgot-password';
import { MapPage } from '../pages/map/map';
import { UserHelpPage } from '../pages/user-help/user-help';
import { UserPrivacyStatementPage } from '../pages/user-privacy-statement/user-privacy-statement';

import { PostComponent } from '../components/post/post';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { Geolocation } from '@ionic-native/geolocation';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    TabsPage,
    PassPage,
    NotificationPage,
    UserPage,
    PostPage,
    NotificationDetailPage,
    ForgotPasswordPage,
    MapPage,
    UserHelpPage,
    UserPrivacyStatementPage,

    PostComponent,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    TabsPage,
    PassPage,
    NotificationPage,
    UserPage,
    PostPage,
    NotificationDetailPage,
    ForgotPasswordPage,
    MapPage,
    UserHelpPage,
    UserPrivacyStatementPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Geolocation,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



