import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the MapPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

declare var google: any;

@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage {

  @ViewChild('map') mapRef: ElementRef;

  map: any;

  currentLat: number = -28.198025;

  currentLon: number = 153.535773;

  currentPost = null;

  posts = [
    {
      "id": 1,
      "user_id": 1,
      "latitude": -28.194694,
      "longitude": 153.533340,
      "visuable_range": 500,
      "is_like": 0,
      "total_likes_count": 45,
      "comments_count": 3,
      "body": 'really like a new game, I\'ll stop playing it mid-way through because I don\'t want it to be over. But then I move onto another great game and never finish the original.',
      "create_at": '2018-07-15 05:57:41',
      "updated_at": '2018-07-15 05:57:41',
    },
    {
      "id": 2,
      "user_id": 1,
      "latitude": -28.196387,
      "longitude": 153.539337,
      "visuable_range": 500,
      "is_like": 0,
      "total_likes_count": 256,
      "comments_count": 13,
      "body": 'Site News: Popular Posts for This Week (07.20.2018): Popular posts from',
      "create_at": '2018-07-15 05:57:41',
      "updated_at": '2018-07-15 05:57:41',
    },
    {
      "id": 3,
      "user_id": 1,
      "latitude": -28.201323,
      "longitude": 153.535711,
      "visuable_range": 500,
      "is_like": 0,
      "total_likes_count": 456,
      "comments_count": 78,
      "body": 'We’re honored to sponsor today’s #DC4D event on #API’s. Make sure you have your ticket ready, it all starts in less then an hour!!!',
      "create_at": '2018-07-15 05:57:41',
      "updated_at": '2018-07-15 05:57:41',
    },
    {
      "id": 4,
      "user_id": 1,
      "latitude": -28.199857,
      "longitude": 153.537996,
      "visuable_range": 500,
      "is_like": 0,
      "total_likes_count": 256,
      "comments_count": 4,
      "body": 'Some community collaboration with the awesome folks of',
      "create_at": '2018-07-15 05:57:41',
      "updated_at": '2018-07-15 05:57:41',
    }
  ];

  postAddress: string = '66/2 Falcon Way, Tweed Heads South NSW 2486';
  curPostTime: string = '06:22';

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    this.loadMap();
  }

  loadMap() {
    const location = new google.maps.LatLng(this.currentLat, this.currentLon);

    const options = {
      center: location,
      zoom: 15,
      zoomControl: false,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: false,
      rotateControl: false,
      fullscreenControl: false,
    };

    this.map = new google.maps.Map(this.mapRef.nativeElement, options);

    this.updateMap();
  }

  addMarker(position, map, icon?) {
    if (icon) {
      return new google.maps.Marker({
        position,
        map,
        icon: icon,
      })
    }

    return new google.maps.Marker({
      position,
      map
    });
  }

  updateMap() {
    // update user current location
    this.updateCurrentLocation(this.currentLat, this.currentLon);

    // update near post
    this.updateNearPosts();
  }

  updateCurrentLocation(latitude: number, longitude: number) {
    this.addMarker(new google.maps.LatLng(latitude, longitude), this.map, './assets/imgs/focus.png');
  }

  updateNearPosts() {
    for (let i = 0; i < this.posts.length; i++) {
      let post = this.posts[i];

      let lat = post.latitude;
      let lon = post.longitude;

      let postMaker = this.addMarker(new google.maps.LatLng(lat, lon), this.map);

      if (postMaker) {
        postMaker.addListener('click', (post => {
          this.currentPost = post;
          console.log(post);
          // this.getGeoName(post.latitude, post.longitude);
          // this.curPostTime = moment(post.created_at).format('HH:mm');
          // this.displayPost(post);
        }).bind(this, post));
      }
    }
  }

  openNewPostPage() {
    this.navCtrl.push("NewPostPage", { latitude: this.currentLat, longitude: this.currentLon });
  }

}



