import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { AppConfig } from '../../app/app.config';

import axios from 'axios';

/**
 * Generated class for the PostPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-post',
  templateUrl: 'post.html',
})
export class PostPage {
  post = null;
  
  postAddress = '';

  curPostTime = '';

  comments = null;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.post = navParams.get('post');
    this.postAddress = navParams.get('postAddress');
    this.curPostTime = navParams.get('curPostTime');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PostPage');
  }
  
  loadComments() {
    axios({
      method: 'get',
      url: AppConfig.getUrl() + '/api/comments',
      headers: {
        'Authorization': AppConfig.getApiAuthorization(),
      }
    }).then(resp => {
      console.log('Get comments success!');
      this.comments = resp;
    }).catch(err => {
      console.log('Get comments fail!');
    });
  }

}



